<template>
  <div id="app">
    <h1>iBatis Struct Dictionary Generator</h1>
	<div class="container">
		<div><span>Java package name:</span><span>&nbsp;<input type="text" v-model="javaPackageName" size="179"/></span></div>
		<div><br/></div>
		<span><textarea rows="30" cols="80" v-model="oracleStructureMsg" /></span>
		<span class="containerButtons">
			<button v-on:click="generateMap">Generate Xml Struct</button>
			&nbsp;
			<button v-on:click="clearGeneratedMap">Clear Xml Struct</button>
		</span>
		<span><textarea rows="30" cols="80" v-model="ibatisMapMsg" readonly/></span>
	</div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
	oracleStructureMsg: "",
	ibatisMapMsg: "",
	javaPackageName: "",
	oracleObjectStructTemplate: "<ORACLE_OBJECT NAME=\"#fwo#\" JAVA_PACKAGE=\"#javaPackage#\">",
	oracleObjectStructAttributeWithType: "<ATTRIBUTE NAME=\"#name#\" JAVA_NAME=\"#javaName#\" JAVA_TYPE=\"#javaType#\"/>",
	oracleObjectStructAttribute: "<ATTRIBUTE NAME=\"#name#\" JAVA_NAME=\"#javaName#\"/>",
    };
  }, 
  methods: {
	upperCaseFirstLetter : function (string) {
		return string.charAt(0).toUpperCase() + string.slice(1);
	},
	lowerCaseAllWordsExceptFirstLetters : function (string) {
		return string.replace(/\w\S*/g, function (word) {
        return word.charAt(0) + word.slice(1).toLowerCase();
    });
	},
    generateMap: function () {
	
		if (this.javaPackageName == null || this.javaPackageName.trim().length == 0) {
			this.$alert("Please specify a Java package name","Invalid input","error");
			return;
		}
	
		if (this.oracleStructureMsg == null || this.oracleStructureMsg.trim().length == 0) {
			this.$alert("Insert an Oracle XML Structure","Invalid input","error");
			return;
		}
	
		var textRows = this.oracleStructureMsg.split('\n');
		textRows = textRows.filter(col => col.trim());
		this.clearGeneratedMap;

		var mapName = textRows[0].replace(/[a-z]/g, '').replace('(','').trim();
		
		let oracleAttributeNames = [];
		let javaNames = [];
		let javaTypes = [];
		
		var attributeName = "";
		var realAttributeName = "";
		for (var i = 1; i < textRows.length-1; i++) {
			attributeName = textRows[i].trim().split(/(?<=^\S+)\s/)
			
			realAttributeName = attributeName[0].split("_");
			
			if (attributeName.length > 1) {
				oracleAttributeNames[i-1] = attributeName[0].trim();
				if (attributeName[1].trim().replace(",","").toLowerCase() == 'integer')
					javaTypes[i-1] = "java.lang.Integer";
				else
					javaTypes[i-1] = "";
			}

			if (realAttributeName.length == 1) {
				javaNames[i-1] = realAttributeName[0].toLowerCase();
			} else {
				javaNames[i-1] = realAttributeName[0].toLowerCase();
				for (var j =1;j<realAttributeName.length;j++) {
					javaNames[i-1] += this.lowerCaseAllWordsExceptFirstLetters(realAttributeName[j]);
				}
			}
		}
		
		var xmlMapResult = "";
		
		//console.log(oracleAttributeNames);
		//console.log(javaTypes);
		//console.log(javaNames);
		
		if (oracleAttributeNames.length && javaTypes.length && javaTypes.length == javaNames.length) {
			
			xmlMapResult = this.oracleObjectStructTemplate.replace("#fwo#", mapName).replace("#javaPackage#",this.javaPackageName);
			xmlMapResult += "\n";
			
			for (var k=0;k<oracleAttributeNames.length;k++) {
				
				if (javaTypes[k] == null || javaTypes[k].trim() == "")
					xmlMapResult += this.oracleObjectStructAttribute.replace("#name#",oracleAttributeNames[k]).replace("#javaName#",javaNames[k]);
				else
					xmlMapResult += this.oracleObjectStructAttributeWithType.replace("#name#",oracleAttributeNames[k]).replace("#javaName#",javaNames[k]).replace("#javaType#",javaTypes[k]);
				
				xmlMapResult += "\n";
			}
			xmlMapResult += "</ORACLE_OBJECT>";
		} else {
			this.$alert("The elements to generate the iBatis structure are not corrected","Invalid values","error");
			return;
		}
	
		this.ibatisMapMsg = xmlMapResult;
    },
	clearGeneratedMap: function () {
		this.ibatisMapMsg = "";
	}
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.container {
    display: inline-block;
}

.containerButtons {
    display: table-cell;
    vertical-align: middle;
	padding-left: 10px;
	padding-right: 10px;
}

.container span {
    display: table-cell;
    vertical-align: middle;
}

.container span textarea {
	border: 1px solid black;
}




</style>
